.btn {
  padding-top: 16px;
  padding-bottom: 16px;
  background-color: #f5f5fa;
  box-shadow: -10px -10px 20px #ffffff, 10px 10px 20px rgba(72, 121, 150, 0.1),
    5px 5px 10px rgba(72, 121, 150, 0.25), -5px -5px 10px #ffffff;
  border-radius: 30.5px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #487996;
  

  &:hover, &:focus  {
    
    box-shadow: -20px -20px 30px #ffffff, 20px 20px 30px rgba(72, 121, 150, 0.1),
    10px 10px 15px rgba(72, 121, 150, 0.25), -10px -10px 15px #ffffff;

    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  &:active {
    background-color: #f3f3fc;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }


  @media screen and (max-width: 767px) {
    width: 100%;
  }

  @media screen and (min-width: 768px) {
    padding-right: 32px;
    padding-left: 32px;
  }

  @media screen and (min-width: 1440px) {
    padding-right: 32px;
    padding-left: 32px;
    font-size: 24px;
  }
}
